.page-body {
    display: flex;
}

.page-title {
    font-size: 4rem;
    color: $bodyColor;
    font-weight: bolder;
    margin-bottom: $block8;
}

.page-content {
    width: 60%;
    padding: $block8;
    h1 {
        margin-top: $block9;
    }
}

@media only screen and (max-width: 728px) {
    .page-content {
        width: 100%;
        padding: $block4;
        h1 {
            margin-top: $block9;
        }
    }
}

.menu-item,
.menu-open-button {
    background: $mint;
    border-radius: 100%;
    width: 50px;
    height: 50px;
    padding: 8px;
    position: fixed;
    color: #ffffff;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -webkit-transition: -webkit-transform ease-out $animationSpeed1;
    transition: -webkit-transform ease-out $animationSpeed1;
    transition: transform ease-out $animationSpeed1;
    transition: transform ease-out $animationSpeed1, -webkit-transform ease-out $animationSpeed1;

    img {
        width: 80%;
    }

}

.menu-open {
    display: none;

}

.lines {
    width: 25px;
    height: 3px;
    background: #596778;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -12.5px;
    margin-top: -1.5px;
    -webkit-transition: -webkit-transform 200ms;
    transition: -webkit-transform 200ms;
    transition: transform 200ms;
    transition: transform 200ms, -webkit-transform 200ms;

}

.line-1 {
    -webkit-transform: translate3d(0, -8px, 0);
    transform: translate3d(0, -8px, 0);
}

.line-2 {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.line-3 {
    -webkit-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
}

.menu-open:checked + .menu-open-button .line-1 {
    -webkit-transform: translate3d(0, 0, 0) rotate(45deg);
    transform: translate3d(0, 0, 0) rotate(45deg);
}

.menu-open:checked + .menu-open-button .line-2 {
    -webkit-transform: translate3d(0, 0, 0) scale(0.1, 1);
    transform: translate3d(0, 0, 0) scale(0.1, 1);
}

.menu-open:checked + .menu-open-button .line-3 {
    -webkit-transform: translate3d(0, 0, 0) rotate(-45deg);
    transform: translate3d(0, 0, 0) rotate(-45deg);
}

.menu {
    position: fixed;
    display: flex;
right: $block8;
bottom: $block8;
margin: auto;
    width: 50px;
    height: 50px;
    text-align: center;
    box-sizing: border-box;
    font-size: 26px;


}

